import React from "react";
import ReplayIcon from "@material-ui/icons/Replay";
import CloseIcon from "@material-ui/icons/Close";
import StarRateIcon from "@material-ui/icons/StarRate";
import FavoriteIcon from "@material-ui/icons/Favorite";
import FlashOnIcon from "@material-ui/icons/FlashOn";
import IconButton from "@material-ui/core/IconButton";

function SwipeButtons() {
   return (
      <>
         <div className="swipeButtons">
            <IconButton className="swipeButtons__replay">
               <ReplayIcon fontSize="large" className="orangy" />
            </IconButton>
            <IconButton className="swipeButtons__close">
               <CloseIcon fontSize="large" className="greeny" />
            </IconButton>
            <IconButton className="swipeButtons__star">
               <StarRateIcon fontSize="large" className="text-warning" />
            </IconButton>
            <IconButton className="swipeButtons__fav">
               <FavoriteIcon fontSize="large" className="pinky" />
            </IconButton>
            <IconButton className="swipeButtons__flash">
               <FlashOnIcon fontSize="large" className="redish" />
            </IconButton>
         </div>
      </>
   );
}

export default SwipeButtons;
