import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import TinderCard from "react-tinder-card";
import axios from "axios";

const TinderCards = () => {
   const localhost = "http://localhost:3005";
   const server = "https://tinder-backend2001.herokuapp.com";
   const [people, setPeople] = useState([]);

   const showCards = () => {
      document.querySelector(".loading_wrapper").style.display = "none";
      document.querySelector(".tinderCards").style.display = "initial";
   };
   const showLoading = () => {
      document.querySelector(".tinderCards").style.display = "block";
      document.querySelector(".loading_wrapper").style.display = "flex";
   };

   useEffect(() => {
      document.querySelector(".tinderCards").style.display = "block";
      async function fetchData() {
         const req = await axios.get(`${server}/card`);
         setPeople(req.data);
         showCards();
      }

      fetchData();
   }, []);

   const swiped = (name) => {
      console.log(`removing ${name}`);
   };
   const swipedOut = (img_count) => {
      if (img_count <= 0) {
         showLoading();
         setPeople([]);
         axios.get(`${server}/card`).then((result) => {
            setPeople(result.data);
            showCards();
         });
      }
   };
   return (
      <>
         <div className="loading_wrapper">
            <div className="lds-dual-ring"></div>
         </div>
         <div className="tinderCards">
            {people.map((obj, index) => {
               return (
                  <TinderCard
                     className="swipe"
                     key={obj.name}
                     preventSwipe={["up", "down"]}
                     onSwipe={() => {
                        swiped(obj.name);
                     }}
                     onCardLeftScreen={() => {
                        swipedOut(index);
                     }}
                  >
                     <div className="tinderCards__wrapper">
                        <div
                           className="tinderCards__img col-12 col-md-10 col-lg-6"
                           style={{
                              backgroundImage: `url('${
                                 process.env.PUBLIC_URL + obj.path
                              }')`,
                           }}
                        >
                           <h3 className="tinderCards__name">{obj.name}</h3>
                        </div>
                     </div>
                  </TinderCard>
               );
            })}
         </div>
      </>
   );
};
export default TinderCards;
